export default{
	data(){
		return{
			logState:["在途中","已揽收","疑难","已签收","退签","同城派送中","退回","转单"],
			expressName:"",
			expressNo:"",
			img:"",
			logusticsObj:{}
		}
	},
	onLoad(e){
		this.img=e.img;
		this.expressName=e.expressName;
		this.expressNo=e.expressNo;
	},
	onShow(){
		let that=this;
		that.$ajax({
			url:"/order/showDelivery",
			data:{
				com:that.expressName,
				nu:that.expressNo
			},
			success(d){
				that.logusticsObj=JSON.parse(d);
				console.log(that.logusticsObj)
			}
		})
	}
}
